.input {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 14px 16px;
  gap: 8px;
  background: #061018;
  border-radius: 8px;
  width: 100%;
  border: 0;
  outline: none;
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #859aad;
  margin-bottom: 8px;
}
.input:last-of-type {
  margin-bottom: 0;
}
